(function () {
    'use strict';

    angular
        .module('redCarpet')
        .controller('BookingController', BookingController);

    /** @ngInject */
    function BookingController($scope, $filter, _, AirportService, AccountService, CartService, TravellersService, $uibModal) {

        var vm = this;

        vm.airportLists = [];
        vm.searchData = {
            account: null,
            numberOfTravellers: 1,
            airport: null
        };
        vm.travellers = TravellersService.travellers;
        vm.totalPrice =  CartService.getTotalPrice() || 0;
        vm.totalItemInCart =  CartService.items.length || 0;
        vm.bookingStep = {
            currentStep: 0
        };

        vm.getPackages = getPackages;
        vm.addToCart = addToCart;
        vm.removePackage = removePackage;
        vm.calculatePackageTotalPrice = calculatePackageTotalPrice;
        vm.goTripDetailStep = goTripDetailStep;
        vm.goBack = goBack;
        vm.goPayment = goPayment;
        vm.pay = pay;
        vm.viewTip = viewTip;
        vm.viewTripDetails = viewTripDetails;

        activate();

        function activate() {

            getAirport();
            getAccount();

        }

        function getAirport() {

            AirportService.getAirport().then(function (res) {
                vm.airportLists = res.data;
                vm.airportLists = $filter('orderBy')(res.data, 'Name');
            }, function () {
                vm.airportLists = [];
            });

        }

        function getAccount() {
            AccountService.getAccount().then(function (res) {
                vm.searchData.account = res.data;
            }, function () {
                vm.searchData.account = null;
            });
        }

        function getPackages() {

            if(!vm.searchData.airport) return;
            vm.loading = true;
            vm.packageLists = [];
            AirportService.getPackages(vm.searchData).then(function (respone) {
                vm.loading = false;
                var newData = respone.data.replace('{"d":null}', '');
                vm.packageLists = angular.fromJson(newData);
            }, function () {
                vm.loading = false;
                vm.packageLists = [];
            })
        }

        function calculatePackageTotalPrice(packages) {

            var price = 0;
            angular.forEach(packages.AirportServices, function (service) {
                price += service.Price;
            });
            return price;
        }

        function addToCart(item) {

            CartService.add(item);

            vm.totalPrice =  CartService.getTotalPrice();
            vm.totalItemInCart =  CartService.items.length;
            vm.packages = CartService.items;

        }

        function removePackage(item) {
            CartService.remove(item);
            vm.packages = CartService.items;
            vm.totalPrice = CartService.getTotalPrice();
            vm.totalItemInCart = CartService.items.length;
            if(!vm.totalItemInCart){
                vm.bookingStep.currentStep = 0;
            }
        }

        function goTripDetailStep() {
            vm.bookingStep.currentStep++;
        }

        function goBack() {
            vm.bookingStep.currentStep--;
            vm.paid = false;
        }

        function goPayment() {
            vm.bookingStep.currentStep++;
        }

        function pay() {
            vm.paid = true;
        }

        function viewTip() {

            $uibModal.open({
                templateUrl: 'app/booking/tip-modal.html',
                size: 'md',
                controller: function ($uibModalInstance) {
                    var vm = this;
                    vm.close = function () {
                        $uibModalInstance.dismiss('cancel');
                    };
                },
                controllerAs: 'vm'
            });
        }

        function viewTripDetails(trip) {

            $uibModal.open({
                templateUrl: 'app/booking/trip-details/trip-details.html',
                controller: 'TripDetailsController',
                controllerAs: 'vm',
                size: 'md',
                resolve: {
                    trip: trip
                }
            });

        }
    }

})();

(function () {
    'use strict';

    angular
        .module('redCarpet')
        .factory('AirportService', AirportService);

    /** @ngInject */
    function AirportService(CONFIG, $http) {

        var service = {
            getAirport: getAirport,
            getPackages: getPackages
        };

        return service;

        function getAirport() {
            return $http({
                method: 'GET',
                url: CONFIG.rest.baseURI + '/GetAirports'
            }).then(function successCallback(response) {
                return response;
            }, function errorCallback(error) {
                return error;
            });
        }

        function getPackages(data) {
            return $http({
                method: 'POST',
                url: CONFIG.rest.baseURI + '/GetPackagesByAirportAndAccount',
                data: data,
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                },
                transformResponse: [function (data) {
                    return data;
                }]
            }).then(function successCallback(response) {
                return response;
            }, function errorCallback(error) {
                return error;
            });
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('redCarpet')
        .factory('AccountService', AccountService);

    /** @ngInject */
    function AccountService(CONFIG, $http) {

        var service = {
            getAccount: getAccount
        };

        return service;

        function getAccount() {
            return $http({
                method: 'GET',
                url: CONFIG.rest.baseURI + '/GetAccount?identifier=2B7C967B-0428-432B-BD98-E5201ECC7EAE'
            }).then(function successCallback(response) {
                return response;
            }, function errorCallback(error) {
                return error;
            });
        }
    }
})();

